import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index';
Vue.use(Router)

function power(url){
  // 当前的URL地址跟所有的菜单中的URL进行比较,返回true或者false
  return store.state.user.menus_url.some(i=>i==url)
}
let router =  new Router({
  routes: [
    {
      path:'/login',
      component:()=>import('../pages/login/login'),
    },
    {
      path:'/',
      component:()=>import('../pages/index/index'),
      children:[
        {
          path:"banner",
          component:()=>import('../pages/banner/banner'),
          name:"轮播图管理",
          beforeEnter(to,from,next){
            power("banner") ? next() : next('/home');
          }
        },
        {
          path:'cate',
          component:()=>import('../pages/cate/cate'),
          name:"商品分类",
          beforeEnter(to,from,next){
            power("cate") ? next() : next('/home');
          }
        },
        {
          path:'goods',
          component:()=>import('../pages/goods/goods'),
          name:"商品管理",
          beforeEnter(to,from,next){
            power("goods") ? next() : next('/home');
          }
        },
        {
          path:'manage',
          component:()=>import('../pages/manage/manage'),
          name:"管理员管理",
          beforeEnter(to,from,next){
            power("manage") ? next() : next('/home');
          }
        },
        {
          path:'member',
          component:()=>import('../pages/member/member'),
          name:"会员管理",
          beforeEnter(to,from,next){
            power("member") ? next() : next('/home');
          }
        },
        {
          path:'menu',
          component:()=>import('../pages/menu/menu'),
          name:"菜单管理",
          beforeEnter(to,from,next){
            power("menu") ? next() : next('/home');
          }
        },
        {
          path:'role',
          component:()=>import('../pages/role/role'),
          name:"角色管理",
          beforeEnter(to,from,next){
            power("role") ? next() : next('/home');
          }
        },
        {
          path:'seckill',
          component:()=>import('../pages/seckill/seckill'),
          name:"秒杀活动",
          beforeEnter(to,from,next){
            power("seckill") ? next() : next('/home');
          }
        },
        {
          path:'spec',
          component:()=>import('../pages/spec/spec'),
          name:"商品规格",
          beforeEnter(to,from,next){
            power("spec") ? next() : next('/home');
          }
        },
        {
          path:'home',
          component:()=>import('../pages/home/home'),
          name:"首页"
        },
        {
          path:'',
          redirect:'home',
        },
      ]
    }
  ]
})

// 全局路由守卫
router.beforeEach((to,from,next)=>{
  // 如果前往登录页面直接走
  if(to.path == '/login'){
    next();
    return;
  }
  // 如果不是去登录页面,首先判断store中的user.user是true就next(),否则user=null,去登录页面
  if(store.state.user){
    next();
    return;
  }
  next('/login');
})

export default router;



