import {requestCateList} from '../../util/request';
export default {
  state:{
    list:[],
  },
  mutations:{
    changeList(state,arr){
      state.list = arr
    },
  },
  actions:{
    changeCateActions(context){
      // 发起axios请求
      // console.log(77);
      requestCateList({istree:true}).then(res=>{
        if(res.data.code == 200){
          context.commit('changeList',res.data.list);
        }
      })
    }
  },
  getters:{
    list(state){
      return state.list
    }
  },
  namespaced:true
}



