import {requestManageList,requestManageCount} from '../../util/request';
export default {
  state:{
    // 显示数据
    list:[],
    // 总条数
    count:0,
    // 当前页码数
    page:1,
    // 每页显示几条
    size:2,
  },
  mutations:{
    changeList(state,arr){
      state.list = arr;
    },
    changeCount(state,count){
      state.count = count;
    },
    changePage(state,n){
      state.page = n;
    }
  },
  actions:{
    manageList(context){
      const params = {
        size:context.state.size,
        page:context.state.page
      }
      // 发起获取列表请求
      requestManageList(params).then(res=>{
        if(res.data.code == 200){
          if((res.data.list == null || res.data.list.length== 0)&&context.state.page>1){
            var page = context.state.page - 1;
             context.commit("changePage",page);
             // 自调
             context.dispatch("manageList")
             return;
         }

          context.commit('changeList',res.data.list);
        }
      })
    },
    changeCountActions(context){
      // 发送获取管理员总数
      requestManageCount().then(res=>{
        if(res.data.code == 200){
          context.commit('changeCount',res.data.list[0].total)
        }
      })
    },
    changePageActions(context,page){
      context.commit('changePage',page)
    }
  },
  getters:{
    list(state){
      return state.list
    },
    count(state){
      return state.count
    },
    size(state){
      return state.size
    },
  },
  namespaced:true
}



