import {requestGoodsList,requestGoodsCount} from '../../util/request';
const state = {
  list:[],
  // 每页显示两条
  size:2,
  // 当前页码数
  page:1,
  count:0
}
const mutations = {
  changeList(state,arr){
    state.list = arr
  },
  changeCount(state,n) {
    state.count = n
  },
  changePage(state,n){
    state.page = n
  }
}
const actions = {
  changeGoodsActions(context,bool){
    let params = null;
    if(!bool){
      params = {
        size:context.state.size,
        page:context.state.page,
      }
    }

    // 发起请求
    requestGoodsList(params).then(res=>{
      if(res.data.code == 200){
        //没有取到数据
        if((res.data.list == null || res.data.list.length== 0)&&context.state.page>1){
         var page = context.state.page - 1;
          context.commit("changePage",page);
          // 自调
          context.dispatch("changeGoodsActions")
          return;
      }

        context.commit('changeList',res.data.list)
      }
    })
  },
  changeCountActions(context){
    requestGoodsCount().then(res=>{
      if(res.data.code == 200){
        context.commit('changeCount',res.data.list[0].total);
      }
    })
  },
  changePageActions(context,page){
    context.commit('changePage',page)
  }
}
const getters = {
  list(state){
    return state.list
  },
  size(state){
    return state.size
  },
  count(state){
    return state.count
  }
}

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced:true
}



