import {requestRoleList} from '../../util/request';
export default {
  state:{
    list:[],
  },
  mutations:{
    changeList(state,arr){
      state.list = arr;
    }
  },
  actions:{
    roleList(context){
      // 发起展示列表请求
      requestRoleList().then(res=>{
        context.commit('changeList',res.data.list)
      })
    }
  },
  getters:{
    list(state){
      return state.list
    }
  },
  namespaced:true


}



