export const state = {
  // user:sessionStorage.getItem('user') ? JSON.parse(sessionStorage.getItem('user')) : null,
  user:sessionStorage.getItem('user') ? JSON.parse(sessionStorage.getItem('user')) : null,

}
export const mutations = {
  changeLogin(state,n){
    // 登录进来,将用户数据存入vuex中
    state.user = n;
    if(n){
      // 同时存入session中
    sessionStorage.setItem('user',JSON.stringify(n));
    }else{
      sessionStorage.removeItem('user');
    }

  }
}
export const getters = {
  user(state){
    return state.user
  }
}



