// 添加分类
export const requestCateAdd = (params)=>{
  // 分类中图片，所以不能在使用qs了，使用new formData
  var formdata=new FormData()
  for(let i in params){
    formdata.append(i,params[i]);
  }
  return axios({
    method:'post',
    url:baseUrl + '/api/cateadd',
    data:formdata
  })
}

