import {requestMenuList} from '../../util/request';
export default {
  state:{
    // 菜单列表
    list:[],
  },
  mutations:{
    changeList(state,arr){
      state.list = arr;
    }
  },
  actions:{
    menuList(context){
      // 发起获取菜单列表请求
      requestMenuList({istree:true}).then(res=>{
        context.commit("changeList", res.data.list);
      })
    }
  },
  getters:{
    list(state){
      return state.list
    }
  },
  namespaced:true
}



